@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab&display=swap);
/* TODO Add site wide styles */

* {
    font-family: 'Roboto Slab', serif;
}

.homepage-layout {
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 100%;
    height: 100%;
    background-color: #f5f5f5fb;

}

.mainpage-container {
    display: flex;
    width: 750px;
    flex-direction: column;
    align-items: center;
    padding-top: 15px;

}

.mainpage-container > h1 {
    font-size: 25px;
    padding-bottom: 8px;
}

.post-button {
    margin-top: 100px;
    width: 150px;
    height: 70px;
    /* background-color: red; */
}

.post-button > button {

}

/* LOGIN PAGE CSS */

.login-container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 800px;
}

.login-container > img {
    position: unset;
    width: 100%;
    height: 100%;
}

.form-container {
    width: 900px;
    height: 500px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    position: absolute;
    align-items: center;
}

.form-info {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 300px;
    height: 400px;
    background-color:#00544F;
    border-radius: 5px;
    align-items: center;

}

.form-img{
    margin: auto;
}

.form-inner {
    margin-top: 80px;
}



.login-input {
    width: 210px;
    margin-top: 15px;
    border-radius: 5px;
}

.navbar-container {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #2C7A7B;
    width: 100%;
    height: 50px;
    color: white;
}

.navbar-content {
    width: 1000px;
    margin-left: 80px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

nav {
    display: flex;
    flex-direction: row;
}

img {
    height: 35px;
    width: 170px;
}

ul {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

li {
    display: flex;
    justify-content: center;
    list-style: none;
}


.navbar-element {
    margin-left: 5px;
    margin-right: 5px;
}

.navbar-element > button:hover {
    font-weight: 600;
    padding-bottom: 1px;
}

.navbar-element:hover {
    font-weight: 600;
    padding-bottom: 1px;
}

.navbar-element a {
    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: center;
}

.footer-container {
    display: flex;
    flex-direction: column;
    height: 400px;
    width: 100%;
    background-color: lightgray;
}

.top {
    height: 60%;
    width: 100%;
    background-color: #F5F5F5;
}

.bottom {
    height: 40%;
    width: 100%;
    background-color: #2C7A7B;
}

.workouts_container {
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 650px;
    padding-top: 10px;
}

.individual-workout_container {
    border-top: 0px;
}

.individual-workout_header{

}

.individual-workout_header:hover{
    color: white;
    transition: 0.5s;
}

.workouts_container button:hover {
    background-color: #3e9697;
}

.workouts_container button:focus {
    box-shadow: none;
}


.individual-workout_data {
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: rgb(233, 233, 233);
}

.excercise-container {
    display: flex;
    flex-direction: column;
    margin-bottom: 15px;
}

.exercise-data {
    display: flex;
    flex-direction: row;
    margin-bottom: 6px;
}

.exercise-data p {
    margin-right: 25px;
}

.creation-button {
    margin-bottom: 10px;
}

.exercise-form_container {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 585px;
    height: 155px;
}


.exercise-form {
    display: flex;
    justify-content: center;
    flex-direction: column;
    border-radius: 5px;
    width: 100%;
    margin-left: 20px;
}

.exercise-form input {
    border-radius: 5px;
    height: 30px;
}

.exercise-form > button {
    width: 90%;
    height: 30px;
    border-radius: 5px;
    margin-top: 5px;
    background-color: #008080;
    color: white;
}

.form-title {
    width: 100%;
}

.form-title > input{
    width: 90%;
    margin-bottom: 5px;
}

.form-nums {
    display: flex;
    flex-direction: row;
    margin-bottom: 5px;
}

.form-nums input {
    width: 28.82%;
    margin-right: 10px;
    border-radius: 5px;
}

.form-notes  input {
    width: 90%;
}

.navlog-container {
    margin-top: 45px;
    height: 200px;
    width: 280px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.navlog-main {
    align-items: center;
    justify-content: center;
    margin: 0;
    padding: 0;
    height: 100%;
    width: 100%;
    border-radius: 5px;
}


.navlog-ul {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(243, 243, 243, 0.932);
    border-style: solid;
    border-width: 1px;
    border-color:  lightgrey;
    border-radius: 5px;
}


.navlog-li {
    display: flex;
    width: 100%;
    height: 50px;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    border-radius: 5px;
}

.navlog-li a {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.navlog-li:hover {
    background-color: lightgrey;
    transition: 0.3s;
}

.navlog-active {
    text-decoration: underline;
}

.entries_container {
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 650px;
    padding-top: 10px;
    border-top: 0px;
}

.entry {
    border-top: 0px;
}

.entries_container button:hover {
    color: white;
    transition: 0.5s;
    background-color: #3e9697
}

.entries_container button:focus {
    box-shadow: none;
}

.entry-data {
    background-color: rgb(233, 233, 233);
}

.meals_container {
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 650px;
    padding-top: 10px;
}

.entry_container {
    border-top: 0px;
}

.entry_container button:hover {
    color: white;
    transition: 0.5s;
    background-color: #3e9697
}

.entry_container button:focus {
    box-shadow: none;
}

.meal-data{
    display: flex;
    flex-direction: row;
}

.meal-data_entry {
    margin-left: 12px;
    width: 100px;
}

